<template>
    <div>
        <div style="display: flex;height: 400px">
            <div style="width: 300px;">
                <div>选择：</div>
                <el-input
                        placeholder="输入关键字进行过滤"
                        v-model="filterText">
                </el-input>
                <el-tree
                        :data="data"
                        show-checkbox
                        node-key="id"
                        ref="tree"
                        highlight-current
                        @check="getChecked"
                        :filter-node-method="filterNode"
                        :props="defaultProps">
                </el-tree>
            </div>
            <div style="width: 300px;" class="ml30">
                <div>已选：</div>
                <ul>
                    <li v-for="item in selectData">{{item.label}}</li>
                </ul>
            </div>
        </div>
        <span slot="footer" class="dialog-footer" style="display: flex;justify-content: center">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="sendData()">确 定</el-button>
        </span>
    </div>
</template>
<script>
    export default {
        watch: {
            filterText(val) {
                this.$refs.tree.filter(val);
            }
        },

        methods: {
            filterNode(value, data) {
                if (!value) return true;
                return data.label.indexOf(value) !== -1;
            },
            getCheckedKeys() {
                console.log(this.$refs.tree.getCheckedKeys());
            },
            getChecked(a, b) {
                this.selectData = []
                b.checkedNodes.map((item) => {
                    if (!item.children) {
                        this.selectData.push(item)
                    }
                })
            },
            sendData() {
                this.dialogVisible = false
                this.$emit('getPersons',this.selectData)
            }
        },

        data() {
            return {
                selectData: [],
                dialogVisible: false,
                filterText: '',
                data: [
                    {
                        id: 1,
                        label: '一级 1',
                        children: [
                            {
                                id: 4,
                                label: '二级 1-1',
                                children: [
                                    {
                                        id: 9,
                                        label: '三级 1-1-1',
                                    }, {
                                        id: 10,
                                        label: '三级 1-1-2',
                                    }
                                ]
                            }
                        ]
                    },
                    {
                        id: 2,
                        label: '一级 2',
                        children: [
                            {
                                id: 3,
                                label: '二级 23',
                                children: [
                                    {
                                        id:14,
                                        label: '三级 123',
                                    }, {
                                        id: 15,
                                        label: 'sakdl',
                                    }
                                ]
                            }
                        ]
                    }
                ],
                defaultProps: {
                    children: 'children',
                    label: 'label'
                }
            };
        }
    };
</script>


